import { Button, Popover, OverlayTrigger } from "react-bootstrap";
import { JournalText, CloudArrowDownFill, ChatSquareTextFill, Archive, ArchiveFill, CameraVideoFill } from "react-bootstrap-icons";
import React from "react";


function PublicationList({ publications }) {
    return (
        <div className="row">
            <div className="col-md-4">
                <PubSectionIcon />
            </div>
            <div className="col-md-8 sec">
                <table>
                    {publications.map((pub) => (
                        <Publication {...pub} />
                    ))}
                </table>
            </div>
        </div>
    );
}

function PubSectionIcon() {
    return (
        <span className="iconSec">
            <JournalText/> PUBLICATIONS
        </span>
    )
}

function Publication({ title, authors, proceeding, location, year, publisher, pdf, abstract, doi, video, arXiv, pages }) {
    let ck = <a href="https://www.cs.cmu.edu/~ckaestne/" target="_blank" rel="noreferrer">Christian KÃ¤stner</a>;
    let cle = <a href="https://clairelegoues.com/" target="_blank" rel="noreferrer">Claire Le Goues</a>;
    let hm = <a href="https://heather.miller.am/" target="_blank" rel="noreferrer">Heather Miller</a>;
    let ar = <a href="https://www.comp.nus.edu.sg/~abhik/" target="_blank" rel="noreferrer">Abhik Roychoudhury</a>;

    const popover = (
        <Popover className="my-popover">
            <Popover.Title as="h3">{title}</Popover.Title>
            <Popover.Content> {abstract} </Popover.Content>
        </Popover>
    );
    let reference;
    if (!proceeding.startsWith("Proceedings")) {
        reference = <td>{authors.join(", ")}. {year}. <b>{title}</b>. <i>{proceeding}</i>.</td>
    }
    else {
        reference = <td> {authors.join(", ")}. {year}. <b>{title}</b>. In <i>{proceeding}</i>, {pages}. {location} {publisher}. </td>
    }
    return (
        <>
            <tr>
                {reference}
            </tr>
            {proceeding.startsWith("PhD Dissertation") && 
            <tr>
                Committee members: {ck}, {cle}, {hm}, {ar}.
            </tr>
            }
            <tr>
                <OverlayTrigger trigger="click" rootClose placement="bottom" overlay={popover}>
                    <Button variant="primary" className="btn-sm icon"><ChatSquareTextFill/> Abstract</Button>
                </OverlayTrigger>
                {typeof pdf !== 'undefined' &&
                    <a href={pdf} target="_blank" rel="noreferrer"><Button variant="success" className="btn-sm icon"><CloudArrowDownFill/> PDF</Button></a>
                }
                {typeof video !== 'undefined' &&
                    <a href={video} target="_blank" rel="noreferrer"><Button variant="info" className="btn-sm icon"><CameraVideoFill/> Video</Button></a>
                }
                {typeof doi !== 'undefined' &&
                    <a href={doi} target="_blank" rel="noreferrer"><Button variant="info" className="btn-sm icon"><ArchiveFill/> DOI</Button></a>
                }
                {typeof arXiv !== 'undefined' &&
                    <a href={arXiv} target="_blank" rel="noreferrer"><Button variant="info" className="btn-sm icon"><Archive/> arXiv</Button></a>
                }
            </tr>
        </>
    );
}

export default PublicationList;